.prereq-section {
    font-family: 'Poppins';
    background-color: var(--baseWhite);
    position: relative;
}

.prereq-section h2 {
    padding-top: 3vw;
    margin-left: 5vw;
    font-size: var(--webHeading);    font-weight: 700;
}

.prereq-tc-r{
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content:space-around ;
}

.prereq-icons {
    display: flex;
    flex-direction: row;
    padding: 0 3vw;
}

.prereq-icons div.prereq-text-containers {
    margin: 5vw 0;
    width: 35%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.prereq-text-containers img {
    max-height: 9vw;
    max-width: 9vw;
}

.prereq-text-containers p {
    margin-top: 2vw;
    text-align: center;
    font-size: 1.4vw;
    font-weight: 400;
}