.ExplorationTicket {
    background-color: var(--baseBlue);
    color: var(--baseWhite);
    padding: 5vw;
}

.ExplorationTicketTitle {
    margin: 0;
    font-weight: 700;
    font-style: normal;
    font-size: var(--webHeading);
}

.ExplorationTicketTitleOrange {
    color: var(--baseOrange);
}

.ExplorationTicketIntro {
    font-size: var(--webNormalFont);
    padding: 2em 0 3em;
    width: 50%;
}

.ExplorationTicketConclude {
    font-size: var(--webNormalFont2);
    color: var(--shade4White);
    text-align: center;
}

.ExplorationTicketIntroDiv {
    display: flex;
    justify-content: space-between;
}

.expticket {
    margin-top: calc(0.4em + 4vw);
    margin-bottom: calc(2em + 1.5vw);
    margin-right: 3vw;
}

.TicketDiv {
    display: flex;
    flex-direction: column;
}

.TicketType {
    width: fit-content;
    font-size: var(--webNormalFont);
    border-top-right-radius: 16px;
    padding: 1vh 2vw 1vh 2.5vw;
    font-weight: 700;
}

.ExpiresOn {
    font-size: var(--webSmallFont);
    font-weight: 300;
}

.TicketInfo {
    width: 66%;
    background-color: var(--baseWhite);
    border-bottom-left-radius: 16px;
}

.TicketInfoGdn {
    width: 66%;
    border-bottom-left-radius: 16px;
    color: transparent;
}

.TicketInfoGdn .TicketTags {
    color: var(--baseWhite);
}

.Bz {
    color: var(--baseBronze);
}

.BzBg {
    background-color: var(--baseBronze);
    color: var(--baseWhite);
}

.BzImg {
    background-size: 80% 75%;
    background-color: var(--baseWhite);
    background-image: url(../../../images/Icons/BronzeTicket.svg);
}

.Sv {
    color: #525B62;
}

.Sv1 {
    color: #878787;
}

.SvBg {
    background-color: var(--baseSilver);
    color: var(--baseBlue);
}

.SvImg {
    background-size: 80% 75%;
    background-color: var(--baseWhite);
    background-image: url(../../../images/Icons/SilverTicket.svg);
}

.GdBg {
    background: linear-gradient(#FFDD4D, #FFE14D);
    color: var(--shade2Golden);
}

div.GdImg {
    background-image: url(../../../images/Icons/GoldenTicket.svg);
    background-size: 85% 75%;
}

.GdTicketBg {
    background: linear-gradient(45deg, #172938 5.33%, #2F4353 59.87%, #13212D 84.55%, #13212D 84.55%);
    border: 1px solid #FFDE4D;
    border-radius: 0 16px 16px 16px;
    overflow: hidden;
}

.TicketImage {
    width: 34%;
    background-repeat: no-repeat;
    background-position: center;
    border-bottom-right-radius: 16px;
    border-top-right-radius: 16px;
}

.TicketHorizontal {
    display: flex;
    height: fit-content;
}

.DotsDiv {
    width: 0;
    margin: 4px 0;
}

.CircularDots {
    border-left: 1.5vw dotted var(--baseWhite);
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
    left: -0.8vw;
}

.TicketContent {
    padding: 3vw 2vw 2vw 2.5vw;
    position: relative;
    color: var(--baseBlue);
}

.InfoHeading {
    font-size: var(--webNormalFont4);
    font-weight: 700;
    margin: 0;
}

.InfoHeadingGdn {
    font-size: var(--mobileMediumFont);
    font-weight: 700;
    margin: 0;
    color: #FECC50;
}

.InfoIntro {
    font-size: var(--webSmallFont5);
    font-weight: 500;
    padding: 0.4vw 6vw 0.4vw 0;
    margin: 0;
}

.TicketInfoGdn .InfoIntro {
    color: var(--baseWhite);
}

.TicketTags {
    display: flex;
    justify-content: space-around;
    padding: 0.5vw 2vw 4vw 2vw;
    color: var(--baseBlue);
}

.TicketTagsCol {
    width: 29%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border: 1.5px solid rgba(188, 193, 197, 0.3);
    border-radius: 8px;
    padding: 25px 10px 15px;
}

.TicketTagImage {
    width: 2.4vw;
    margin-bottom: 1.2vw;
}

.TicketTagDetail {
    font-size: var(--webSmallFont);
    text-align: center;
}

.ExplorationTicket ul {
    list-style: none;
    position: relative;
    padding: 0;
    margin-top: 5vw;
    margin-bottom: 0;
}

.ExplorationTicket ul::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 67%;
    top: 0;
    border-width: 4px;
    border-style: solid;
    border-image: linear-gradient(to bottom, #DB8F45, #9D9D9D, #FFE23D) 1 100%;
    padding: 1.3vw;
}

.ExplorationTicket ul::after {
    content: "";
    position: absolute;
    height: 67%;
    top: 0;
    padding: 1.6vw;
    background: linear-gradient(to bottom, var(--baseWhite)0%, rgba(0, 0, 0, 0)60%) 100%;
    background-position: center;
    background-size: 3px 3.5vw;
    background-repeat: repeat-y;
}

.ExplorationTicket ul li {
    padding: 0 2vw 10vw 2.8vw;
    position: relative;
}

.ExplorationTicket ul li:nth-child(3) {
    padding: 0 2vw 7vw 2.8vw;
}

.ExplorationTicket li::before {
    position: absolute;
    z-index: 1;
    height: fit-content;
    width: fit-content;
    left: -1.9vw;
    top: -3.5vw;
    padding: 3.1vw 3.1vw;
    text-align: center;
    display: block;
    content: ".";
    background-position: center;
    background-size: cover;
    color: var(--baseBlue);
    font-size: var(--webNormalFont);
}

.ExplorationTicket li:nth-child(1)::before {
    background-image: url(../../../images/Icons/MapMarkerBz.svg);
}

.ExplorationTicket li:nth-child(2)::before {
    background-image: url(../../../images/Icons/MapMarkerSv.svg);
}

.ExplorationTicket li:nth-child(3)::before {
    background-image: url(../../../images/Icons/MapMarkerGd.svg);
}

.centerbtn {
    margin-left: auto;
    margin-right: auto;
    margin-top: 3vw;
}

.GdBtn {
    background: linear-gradient(to right, #FFDD4D, #FFE14D);
    color: var(--shade2Golden);
    border: 1px solid var(--shade2Golden);
    font-weight: 600;
    padding: 0.5em 1.5em;
}

.GdTicketBg .TicketTagsCol {
    padding: 17px 10px 11px;
}