.det-trip-dest-page {
    display: flex;
    justify-content: center;
    background-color: var(--baseWhite);
}

.det-trip-dest-content {
    position: absolute;
    margin-top: 3vw;
    padding: 3vw;
}

.det-trip-dest-heading {
    display: flex;
    align-items: center;
    font-family: 'Poppins';
    font-weight: 700;
    font-size: var(--webHeading);
    width: 24vw;
}

.det-trip-dest-heading img {
    margin-bottom: 1vw;
    padding-right: 0.4vw;
    padding-bottom: 0.3vw;
    padding-top: 1vw;
    height: 70%;
    width: 20%;
}

.det-trip-dest-heading span.orange-text {
    padding-left: 0.5vw;
}

.det-trip-dest-subheading {
    font-family: 'Poppins';
    font-weight: 400;
    color: var(--shade4Black);
    padding-left: 0.8vw;
    padding-top: 1vw;
    font-size: var(--webSmallFont2);
}

.det-trip-grid-encloser {
    width: 100%;
    display: flex;
    justify-content: center;
}

.det-trip-grid {
    margin-top: 2vw;
    margin-bottom: 3vw;
}

.det-trip-grid div.col {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    align-items: center;
    justify-content: space-between;
    padding: 0 1vw 0 2vw;
    margin: 1.5vh 0 1.5vh;
}

.det-trip-grid div.col img {
    width: 9vw;
}

.det-trip-grid div.col section.dest-desc {
    font-family: 'Poppins';
    text-align: center;
    color: gray;
    font-size: 1.3vw;
    width: 95%;
}

.col section.dest-desc {
    padding: 0;
}

.col section.dest-desc h3 {
    color: var(--baseOrange);
    font-size: var(--webNormalFont4);
}

.det-trip-dest-page .button {
    margin-top: 3vw;
}

img.det-trip-dest-bg {
    width: 100%;
}