.det-intro-section {
	position:relative;
    background-color: var(--baseBlue);
    display: flex;
    align-items: center;
}

.det-intro-section img.det-bg {
    width: 100%;
    height: 100vh;
}

.det-intro {
    position: absolute;
    display: flex;
    flex-direction: row;
    margin: 10vh 5vw;
    width: 90%;
}

.det-intro-text {
    display: flex;
    flex-direction: column;
    font-family: 'Poppins';
    margin-top: 3vh;
}

.det-intro-icons {
    display: flex;
    padding: 0 8vw;
    flex-direction: column;
    height: 100%;
    flex: 0.5 0 3vw;
    margin-top: 3vh;
}

.det-intro-icons-r1{
    display: flex;
    flex-direction: column;
}

.det-intro-text h2 {
    color: var(--baseWhite);
    font-size: var(--webXlHeading);
    margin-bottom: 1vw;
    margin-top: 1.2vw;
    margin-left: 3vw;
    font-weight: 700;
    line-height: 1.4em;
}

.det-intro-text p {
    color: var(--shade4White);
    width: 65%;
    font-weight: 400;
    font-size: var(--webNormalFont);
}

.det-intro-icons img.det-intro-icon {
    flex: 0 1 5vw;
    max-width: 40%;
}

.det-intro-icons img.skewed-orientation {
    margin-left: auto;
}

.pillarimg{
    width: 37vw;
}